.body {
  background: #f0f0f0;
}

/*
Nav bar
*/
.navbar-container{
  background: #500000;

}
.navbar{
  width: 60%;
  margin: auto;
  height: 56px;

}
.navbar-name{
  color: #fff;
  float: left;
}
.navbar-links{
  color: #fff;
  float: right;
}

.navbar-links li{
  float: left;
  list-style-type: none;
  padding-right: 15px;
  padding-left: 15px;
}

.navbar-links li a{
  color: #fff;
  text-decoration: none;
}

.navbar-links li a:hover{
  text-decoration:  underline;
}
/*
Content
*/
#content{
  margin: auto;
  width: 60%;
}

.stores{
  width: 100%;
  padding-top: 40px;
  padding-bottom: 40px;
  display: inline-block;
}
.store-row{
  background: #500000;
  color: #FFFFFF;
  height: 100px;
  padding-top: 20px;
  padding-bottom: 20px;
  border-radius: 20px;
  padding-left: 50px;
  margin-bottom: 10px;
  text-align: left;
}
.store-name{
  font-size:2em;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
}

.store-info{
  float: left;
}
.graph{
  float: right;
}


@media screen and (max-width: 600px) {
  .store-name {
    font-size: 1.1em;
  }
  .stores{
    padding-top: 0px
  }
  #content{
    width: 90%;
  }
}
